<template>
    <div class="live-detail" ref="detail" :class="{'to-fix': toFix}">
        <div class="video">
            <div class="top-shadow">
                <i class="go-back" @click="back"> </i>
            </div>
            <player v-if="program.liveStatus == 0 || program.liveStatus == 2" :src='src' :isLive="program.liveStatus == 0" :poster="imgFilter(program.posterUrl, '!750x422r', 'Center', '750x422')"></player>
            <div class="post-img" v-else>
                <img v-lazy="imgFilter(program.posterUrl, '!750x422r', 'Center', '750x422')" alt="">
                <count-down @startPlay="startPlay" class="count-down" v-if="program.timeToStart" :time-to-start="program.timeToStart"></count-down>
            </div>
        </div>
        <div id="comments" v-if="!isShowDesc">
            <div class="main-infos border-bottom-1px">
                <span class="tag" :class="'tag' + program.liveStatus">{{liveStatusTag[program.liveStatus]}}</span>
                <p class="title">{{program.subject}}</p>
                <p v-if="program.content" class="desc" @click="showDesc()">简介<i></i></p>
            </div>
            <div class="infos">
                <span v-if="program.tags" class="tag"><i></i>{{program.tags && program.tags[0]}}</span>
                <span v-if="program.liveStatus == 1" class="time">开播时间：{{program.startTime}}</span>
                <span v-if="program.liveStatus == 0 || program.liveStatus == 2" class="time">{{program.viewNum}} 人观看</span>
                <span v-if="program.liveStatus == 0" class="op" @click="switchLine">
                    <i>{{line + 1}}</i>
                    切换线路
                </span>
            </div>
            <div class="comments">
                <div class="title border-top-1px">
                    <p>评论</p>
                    <ul>
                        <li :class="{'active': commentOrder == 'id desc'}" @click="setCommentOrder('id desc')">全部</li>
                        <li :class="{'active': commentOrder == 'like_num desc'}" @click="setCommentOrder('like_num desc')">最热</li>
                    </ul>
                    <p class="comment-num">{{totalElements || 0}} 条热评</p>
                </div>
                <!-- <loadmore :bottom-method="loadBottom" :bottom-all-loaded="allLoaded" ref="loadmore" :auto-fill="autoFill"> -->
                    <ul
                        v-infinite-scroll="loadBottom"
                        infinite-scroll-disabled="loadMore"
                        infinite-scroll-distance="300"
                        infinite-scroll-immediate-check
                        class="contents" 
                        ref="loadmoreComments">
                        <li v-for="(item, index) in comments">
                            <img v-lazy="thumbnail(item.avatar, '5', '72', '72')" alt="">
                            <div v-if="item" class="head border-bottom-1px">
                                <p class="name">
                                    <span>{{item.userName}}</span>
                                    <span class="time">{{item.createTime}}</span>
                                </p>
                                <p class="pride" :class="{'active': item.userLiked == 1}">
                                    <span class="delete" @click="deleteComment(item.id, index)" v-if="item.enableDel">删除</span>
                                    <span>{{item.likeNum}}</span>
                                    <i class="pride" @click="pride(item.id, item.userLiked, index)"></i>
                                </p>
                                <p v-if="item" class="content">{{item.content}}</p>
                            </div>
                        </li>
                    </ul>
                    <p v-if="loadMore && !allLoaded" class="page-infinite-loading">
                        <!-- <mt-spinner type="fading-circle"></mt-spinner> -->
                        加载中...
                    </p>
                <!-- </loadmore> -->
                <div v-if="loading == 3 || loading == 2" class="load-more" :class="{'no-data': loading == 3}">{{loading ==1 ? '加载中...' : loading == 2 ? '没有更多数据了' : loading == 3 ? '暂无评论' : '加载更多'}}</div>
            </div>            
        </div>
        <template v-else>
            <div class="desc-title border-bottom-1px">
                <span class="title">简介</span>
                <span class="close" @click="showDesc()"></span>
            </div>
            <div class="desc-content">{{program.content}}</div>
        </template>
        <div class="comment border-top-1px" ref="inputToComment">
            <mt-checklist
                title=""
                v-model="anonymous"
                :options="options">
            </mt-checklist>
            <div class="editor-wrap" :class="{'comment-to-op': !isShowComment}">
                <div class="editor-inner-wrap">
                    <div class="editor border-bottom-1px" contenteditable="true" ref="editor" @focus="onFocus" @blur="onBlur" @input="changeText"></div>
                </div>
                
            </div>
            <template v-if="isShowComment">
                <div class="share">
                    <i></i>
                    <span @click="onShowShare">分享</span>
                </div>
                <div class="collection">
                    <i :class="{'active': isCollected}"></i>
                    <span :class="{'active': isCollected}" @click="collect">收藏</span>
                </div>
            </template>
            <p v-else @click="comment" class="comment-op">发表</p>
        </div>
        <div v-if="shareVisible" class="share-tip" @click="onShowShare">
            <div class="share-content">
                <div v-if="isWeixin" class="is-weixin">
                    <span class="top-arrow"></span>
                    <div class="tips">
                        <p class="tip tip-andr">
                            点击上方的“<i class="lines"></i>”分享到朋友圈
                        </p>   
                    </div>                   
                </div>
                <div v-else-if="isIos" class="not-weixin">
                    <div class="tips">
                        <p class=" tip tip-safari">
                            safari浏览器请点击下方的“<i class="top"></i>”按钮分享到朋友圈
                        </p>    
                    </div>
                    <span class="bottom-arrow"></span>                    
                </div>
                <div v-else class="weixin">
                    <span class="top-arrow"></span>
                    <div class="tips">
                        <p class="tip tip-andr">
                            点击浏览器上方的“<i class="lines"></i>”分享到朋友圈
                        </p>    
                    </div>                    
                </div>
            </div>
            <div class="share-shadow"></div>
        </div> 
    </div>
</template>

<script>
import {mapActions, mapGetters} from 'vuex';
import player from '../component/video/video.vue'
import countDown from '../fragment/count-down.vue';
import headerView from '../fragment/header/header.vue'
import loadmore from '../component/loadmore';
export default {
    props: ['data', 'params'],
    components: {
        headerView,
        player,
        countDown,
        loadmore
    },
    data() {
        return {
            isLogin: this.$store.state.token,
            loadMore: false,
            allLoaded: false,
            autoFill: false,
            options: [{label: '匿名', value: 1}],
            anonymous: [],
            shareVisible: false,
            isShowDesc: false,
            content: '快来发表你的感想吧',
            comments: [],
            totalElements: 0,
            bizSource: 'program',
            title: '详情',
            liveStatusTag: {
                0: '直播',
                1: '预告',
                2: '回放'
            },
            commentOrder: 'id desc',
            loading: 0,// 0-可以加载， 1-加载中， 2-没有更多数据， 3-暂无数据
            offset: 0,
            pageSize: 10,
            isCollected: this.data.isCollect,
            isShowComment: true,
            program: this.data.program,
            isWeixin: false,
            isAndroid: false,
            isIos: false,
            tryCount: 0,
            src: '',
            line: 0,
            toFix: false,
        };
    },
    created() {
        // this.commentRecords();
        let u = navigator.userAgent;
        this.isAndroid = u.indexOf('Android') > -1 || u.indexOf('Adr') > -1; //android终端
        this.isIos = !!u.match(/\(i[^;]+;( U;)? CPU.+Mac OS X/); //ios终端
        this.isWeixin = u.indexOf('MicroMessenger') > -1;
        this.setSrcLine();
    },
    computed: {
        addrs: {
            get() {
                return this.data.program.addrs;
            },                
            set(newVal) {
                if(this.program.liveStatus == 0) {
                    this.src = this.data.program.addrs.rtmpAddrAppPrimary &&  this.data.program.addrs.rtmpAddrAppPrimary.auto;
                    this.line = 0;
                } else if(this.program.liveStatus == 1) {
                    this.src = '';
                    this.line = 0;
                } else {
                    this.src = this.data.program.addrs.recAddrApp &&  this.data.program.addrs.recAddrApp.addr;
                }
            }
        }
    },
    methods: {
        setSrcLine() {
            if(this.program.liveStatus == 0) {
                this.src = this.data.program.addrs.rtmpAddrAppPrimary &&  this.data.program.addrs.rtmpAddrAppPrimary.auto;
                this.line = this.data.program.addrs.rtmpAddrAppPrimary &&  this.data.program.addrs.rtmpAddrAppPrimary.backedNo;
            } else if(this.program.liveStatus == 1) {
                this.src = '';
                this.line = 0;
            } else {
                this.src = this.data.program.addrs.recAddrApp &&  this.data.program.addrs.recAddrApp.addr;
                this.line = 0;
            }            
        },
        onFocus() {
            if(!this.isLogin) {
                this.$router.push({name: 'login', sid: ''});
            }
            this.isShowComment = false;
            if(this.content == '快来发表你的感想吧') {
                this.$refs.editor.innerText = ' ';
            }
            if(this.isIos) {
                this.toFix = true;
                // document.body.scrollTop = document.body.clientHeight;
                document.documentElement.scrollTop = document.documentElement.scrollHeight + 100;
            }
            
            // setTimeout(() => {
            //     let inputPannel = this.$refs.inputToComment;
            //     inputPannel.scrollIntoView(true);
            //     inputPannel.scrollIntoViewIfNeeded();
            // }, 200)
        },
        onBlur() {
            if(this.isIos) {
                this.toFix = false;
            }
        },
        changeText() {
            if(this.isIos) {
                this.toFix = true;
            }
            this.isShowComment = false;
            this.content = this.$refs['editor'].innerText;
            // setTimeout(() => {
            //     let inputPannel = this.$refs.inputToComment;
            //     inputPannel.scrollIntoView(true);
            //     inputPannel.scrollIntoViewIfNeeded();
            // }, 200)
        },
        startPlay() {
            this.ajax('liveDetail', {
                sid: this.$route.params.sid
            }, null, 'get').then((res) => {
                this.program = res.data.data.program;
                this.isCollected = res.data.data.isCollect;
                this.addrs = this.program.addrs;
            });
        },
        thumbnail(img, method, w, h) {
            if(!img) {
                return 'http://s2.jsyxw.cn/yanxiu/h2.jpg';
            } else {
                return img + '?imageView2/' + method + '/w/' + w + '/h/' + h;
            }
        },
        imgFilter(img, thumbnail, gravity, crop) {
            if(!img) {
                return 'http://live.yanxiu.com/image/3.jpg';
            } else {
                return img + '?imageMogr2/thumbnail/' + thumbnail + '/gravity/' + gravity + '/crop/' + crop;
            }
            
        },
        loadBottom () { // 加载更多数据的操作
            this.loadMore = true;
            this.tryCount ++;
            // setTimeout(() => {
                this.loading = 1;
                let queries = {
                    bizSource: this.bizSource,
                    bizId: this.program.sid,
                    offset: this.offset,
                    pageSize: this.pageSize,
                    orderBy: this.commentOrder
                }
                this.ajax('commentRecords', queries, null, 'get').then((res) => {
                    if(res.data && res.data.code == 0 || res.res && res.res.data.data.code == 0) {
                        let data = res.data && res.data.data || res.res && res.res.data.data;
                        if(data) {
                            this.tryCount = 0;
                            this.totalElements = data.totalElements || 0;
                            this.comments.push.apply(this.comments, data.elements && data.elements || []); 
                            if(data.elements.length < this.pageSize) {
                                this.loading = 2;
                                this.allLoaded = true;
                                this.loadMore = true;
                                if(this.totalElements == 0) {
                                  this.loading = 3;
                                }
                            } else {
                                this.loading = 0;
                                this.loadMore = false;
                                this.offset += this.pageSize;
                            }
                        } else {
                            this.loading = 0;
                            this.loadMore = false;
                            if(this.tryCount == 5) {
                                this.loading = 3;
                                this.allLoaded = true;
                                this.loadMore = true;
                            }
                        }
                        // this.$refs.loadmore.onBottomLoaded();
                    } 
                });
            // },1000)
        },
        switchLine() {
            if(this.line == 0) {
                this.src = this.program.addrs.rtmpAddrAppBackup1 &&  this.program.addrs.rtmpAddrAppBackup1.auto;
                this.line = 1;
            } else if(this.line == 1) {
                this.src = this.program.addrs.rtmpAddrAppPrimary &&  this.program.addrs.rtmpAddrAppPrimary.auto;
                this.line = 0;
            }
        },
        onShowShare() {
            this.shareVisible = !this.shareVisible;
        },
        ajax(name, queries, data, method) {
            return this.$store.dispatch('proxyAction', {
                'name': name,
                'message': false,
                'queries':queries,
                'data': data,
                'method': method
            })    
        },
        back() {
            if(this.isWeixin || window.history.length == 0) {
                this.$router.push({name: 'home', sid: ''});
            } else {
                this.$router.go(-1);                
            }
        },
        commentRecords(id) {
            if(id) {
                this.offset = 0;
                this.comments = [];
                this.allLoaded = false;
            }
            let queries = {
                bizSource: this.bizSource,
                bizId: this.program.sid,
                offset: this.offset,
                pageSize: this.pageSize,
                orderBy: this.commentOrder
            }
            this.ajax('commentRecords', queries, null, 'get').then((res) => {
                if(res.data && res.data.code == 0 || res.res && res.res.data.data.code == 0) {
                    let data = res.data && res.data.data || res.res && res.res.data.data;
                    this.totalElements = data && data.totalElements;
                    this.comments.push.apply(this.comments, data && data.elements || []);
                    this.offset += this.pageSize;
                    if(this.comments.length == 0) {
                        this.loading = 3;
                        this.allLoaded = true;
                        this.loadMore = true;
                    } else if(data) {
                        if(data.elements.length < this.pageSize || data.elements.length == 0) {
                            this.loading = 2;
                            this.allLoaded = true;
                            this.loadMore = true;
                        } else {
                            this.loading = 0;
                            this.allLoaded = false;
                            this.loadMore = false;
                        }                        
                    }
                } 
            });
        },
        comment() {
            if(!this.isLogin) {
                this.$router.push({name: 'login', sid: ''});
            }
            let data = {
                bizSource: this.bizSource,
                bizId: this.program.sid,
                content: this.content,
                anonymous: this.anonymous.length > 0 && this.anonymous[0] == 1 || false
            }
            const me = this;
            this.ajax('saveUserComment', null, data, 'post').then((res) => {
                let data = res.data || res.res && res.res.data.data;
                if(data.code == 0) {
                    document.body.scrollTop = 0;
                    document.documentElement.scrollTop = 0;
                    let data = res.data && res.data || res.res && res.res.data.data;
                    this.comments.splice(0, 0, data.data);
                    this.totalElements = this.totalElements ? ++ this.totalElements : 1 ;
                    this.isShowComment = true;
                    this.content = '';
                    this.loading = 2;
                    this.$refs['editor'].innerText = '';
                    if(this.isIos) {
                        this.toFix = false;
                    }
                }
            });
        },
        collect() {
            if(!this.isLogin) {
                this.$router.push({name: 'login', sid: ''});
            }
            let name = this.isCollected ? 'cancelCollection' : 'collection';
            let queries = {
                bizSource: this.bizSource,
                bizId: this.program.sid,
            }
            let me = this;
            this.ajax(name, queries, null, 'get').then((res) => {
                let data = res.data || res.res && res.res.data.data;
                if(data.code == 0) {
                    let data = res.data && res.data.data || res.res && res.res.data.data;
                    this.isCollected == 0 ? this.isCollected = 1 : this.isCollected = 0;
                }
            })
        },
        deleteComment(id, index) {
            if(!this.isLogin) {
                this.$router.push({name: 'login', sid: ''});
            }
            let queries = {
                bizSource: this.bizSource,
                bizId: this.program.sid,
                recordId: id
            };
            const me = this;
            this.ajax('deleteUserComment', queries, null, 'get').then((res) => {
                let data = res.data || res.res && res.res.data.data;
                if(data.code == 0) {
                    this.comments.splice(index, 1);
                    this.totalElements --;
                }
            });
        },
        pride(id, userLiked, index) {
            if(!this.isLogin) {
                this.$router.push({name: 'login', sid: ''});
            }
            let name = 'likeCommentRecord'
            if(userLiked == 1) {
                name = 'cancelLikeCommentRecord';
            }
            let queries = {
                bizSource: this.bizSource,
                bizId: this.program.sid,
                recordId: id
            };
            const me = this;
            this.ajax(name, queries, null, 'get').then((res) => {
                let data = res.data || res.res && res.res.data.data;
                if(data.code == 0) {
                    let temp = {
                        likeNum: '',
                        userLiked: 0
                    }
                    if(name == 'likeCommentRecord') {
                        temp = {
                            likeNum: this.comments[index].likeNum + 1,
                            userLiked: 1
                        }
                    } else {
                        temp = {
                            likeNum: this.comments[index].likeNum - 1,
                            userLiked: 0
                        }  
                    }
                    this.comments.splice(index, 1, Object.assign(this.comments[index], temp));
                }
            });                
        },
        setCommentOrder(order) {
            this.loading = 0;
            this.commentOrder = order;
            this.loadMore = false;
            this.offset = 0;
            this.offset = 0;
            this.comments = [];
            this.allLoaded = false;
            this.commentRecords(true);
        },
        showDesc() {
            this.isShowDesc = !this.isShowDesc;
        }
    }
};
</script>
<style lang="scss">
.live-detail {
    .mint-checklist {
        max-width: 200px;
        position: absolute;
        bottom: 22px;
        left: 30px;
        max-height: 48px;
        .mint-checklist-title {
            display: none;
            margin: 0;
        }
        .mint-cell {
            height: 48px;
            min-height: 40px;
            background: #fff;
        }
        .mint-cell-title {
            
        }
        input {
            outline: 0;
        }
        .mint-cell-wrapper, .mint-checklist-label {
            padding: 0;
        }
    }
}
</style>
<style scoped lang="scss">
    @import '../lib/mixin.scss';
    .live-detail {
        &.to-fix {
            overflow: hidden;
            height: 100vh;
            .video {
                /* position: absolute;
                top: 0px; */
            }
            .comment {
                position: absolute;
                bottom: 0px;
            }
        }
        position: relative;
        .mint-popup {
            background: transparent;
            width: 100%;
            height: 100%;
        }
        .share-tip {
            .share-shadow {
                position: fixed;
                top: 0;
