                background: rgba(0, 0, 0, 0.8);
                z-index: 500;
            }
            .share-content {
                z-index: 501;
                position: fixed;
                color: #fff;
                top: 0;
                right: 0;
                bottom: 0;
                left: 0;
                .top-arrow, .bottom-arrow {
                    display: block;
                    width: 88px;
                    height: 164px;
                    position: fixed;
                    &.top-arrow {
                        background: url('./img/arrow-t.png') no-repeat center center;
                        background-size: contain;
                        top: 100px;
                        right: 85px;
                    }
                    &.bottom-arrow {
                        background: url('./img/arrow-b.png') no-repeat center center;
                        background-size: contain;
                        bottom: 160px;
                        right: 326px;
                    }
                }
                .tips {
                    width: 100%;
                    position: fixed;
                    bottom: 585px;
                    left: 50%;
                    margin-left: -375px;
                    p {
                        text-align: center;
                        line-height: 65px;
                        font-size: 32px;
                        i {
                            display: inline-block;
                            width: 26px;
                            margin: 0 15px;
                            &.lines {
                                height: 16px;
                                background: url('./img/share-lines.png') no-repeat bottom center;
                                background-size: contain;
                            }
                            &.top {
                                height: 36px;
                                background: url('./img/share-arrow.png') no-repeat bottom center;
                                background-size: contain;
                            }
                            
                        }
                    }    
                }
                .is-weixin {
                    .tips {
                        top: 385px;
                    }
                }
            }            
        }

        #comments {
            position: absolute;
            top: 422px;
            bottom: 101px;
            left: 0;
            right: 0;
        }
        .video {
            height: 422px;
            position: fixed;
            top: 0;
            left: 0;
            right: 0;
            z-index: 101;
            background: #000;
            .top-shadow {
                position: absolute;
                top: 0;
                left: 0;
                right: 0;
                width: 750px;
                height: 72px;
                z-index: 102;
                background: -webkit-linear-gradient(top,rgba(0,0,0,0.3),rgba(0,0,0,0)); /* Safari 5.1 - 6 */
                background: -o-linear-gradient(bottom,rgba(0,0,0,0.3),rgba(0,0,0,0)); /* Opera 11.1 - 12*/
                background: -moz-linear-gradient(bottom,rgba(0,0,0,0.3),rgba(0,0,0,0)); /* Firefox 3.6 - 15*/
                background: linear-gradient(to bottom, rgba(0,0,0,0.3), rgba(0,0,0,0)); /* 标准的语法 */            
            }
            i.go-back {
                z-index: 103;
                display: block;
                width: 25px;
                height: 45px;
                position: absolute;
                top: 27px;
                left: 53px;
                background: url('./img/detail-back.png') no-repeat left top;
                background-size: contain;
            }
            .post-img {
                /* margin-top:190px;
                font-size: 28px;
                text-align: center;
                color: #fff; */
                position: relative;
                width: 750px;
                height: 422px;
                img {
                    width: 750px;
                    height: 422px;
                    object-fit: cover;
                    position: absolute;
                    top: 0;
                    left: 0;
                }
                .count-down {
                    position: absolute;
                    bottom: 30px;
                    left: 30px;
                }
            }
        }
        .main-infos {
            @include border-bottom-1px(#ccc)
            padding: 30px;
            overflow: hidden;
            /* height: 80px; */
            background-color: #fff;
            .tag {
                font-size: 20px;
                background: rgba(16,173,156,1);
                width: 70px;
                display: inline-block;
                height: 30px;
                line-height: 32px;
                text-align: center;
                color: #fff;
                border-radius: 6px;
                float: left;
                margin-top: 7px;
                &.tag0 {
                    background: rgba(236,115,104,1);
                }
                &.tag2 {
                    background: rgba(240,238,238,1);
                    color: #333;
                }
            }
            p {
                float: left;
                &.title {
                    width: 480px;
                    font-size: 32px;
                    color: #333;
                    margin: 0 35px 0 22px;
                    line-height: 40px;
                    height: 80px;
                    overflow: hidden;
                }
                &.desc {
                    font-size: 24px;
                    color: #999;
                    margin-top: 7px;
                    i {
