var RemoveControlNames = ["volumePanel", "fullscreenToggle", "durationDisplay", "progressControl", "currentTimeDisplay"],
    addControlNames = ["currentTimeDisplay", "progressControl", "durationDisplay", "fullscreenToggle"],
    liveHideNames = ["currentTimeDisplay", "progressControl", "durationDisplay"];

var mixin = {
    mounted: function mounted() {
        this.$player = this.$refs.videoPlayer.player;
        //删除默认组件
        removeControlBar.call(this);
        //添加自定义组件
        addControlBar.call(this, addControlNames);
        if (this.isLive) {
            hideControlBar.call(this);
        }
        addUserPanal.call(this);
    }
};

var hideControlBar = function hideControlBar() {
    var _this = this;

    liveHideNames.forEach(function (name) {
        _this.$player.controlBar.getChild(name).el().style.visibility = "hidden";
    });
};
var removeControlBar = function removeControlBar() {
    var _this2 = this;

    RemoveControlNames.forEach(function (name) {
        _this2.$player.controlBar.removeChild(name);
    });
    this.$player.removeChild("bigPlayButton");
};

var addControlBar = function addControlBar(controlNames) {
    var _this3 = this;

    controlNames.forEach(function (name) {
        _this3.$player.controlBar.addChild(name);
    });
};

var pool = {};

var h = function h(tagName) {
    var props = arguments.length > 1 && arguments[1] !== undefined ? arguments[1] : {};
    var children = arguments.length > 2 && arguments[2] !== undefined ? arguments[2] : [];

    var _el = document.createElement(tagName),
        id = props["id"];
    if (!id) throw "require id in props";

    //set attr
    for (var name in props) {
        var _propValue = props[name];
        _el.setAttribute(name, _propValue);
    }
    // render Children
    children.forEach(function (tag) {
        _el.appendChild(tag);
    });

    //cash
    pool[id] = _el;
    return _el;
};

var addUserPanal = function addUserPanal() {
    var _this4 = this;

    var el = h("div", {
        class: "user-panel",
        id: "userPanel"
    }, [h("div", {
        class: "box",
        id: "box"
    }, [h("div", {
        class: "play-icon",
        id: "playIcon"
    }, [h("div", {
        class: "play",
        id: "play"
    })])])]);

    var container = document.querySelector(".video-js");
    container.appendChild(el);

    var $icon = pool["playIcon"],
        $play = pool["play"],
        $userPanel = pool["userPanel"];

    var show = function show() {
        $userPanel.style.display = "block";
    };

    var hide = function hide() {
        $userPanel.style.display = "none";
    };

    this.$player.on("useractive", function (e) {
        show();
    });
    this.$player.on("userinactive", function (e) {
        hide();
    });

    this.$player.controlBar.getChild("fullscreenToggle").on("tap", function () {
        //    if(this.$player.isFullscreen_){
        //        $userPanel.setAttribute("class","user-panel full")
        //    }
        //    else{
        //        $userPanel.setAttribute("class","user-panel")
        //    }
    });
    //    window.onresize = function() {
    //         if (!exitFull()) {
    //             //要执行的动作
    //             $userPanel.setAttribute("class","user-panel")
    //         }
    //     }
    //无法直接横屏
    $icon.addEventListener("click", function () {
        if (_this4.$player.techGet_("paused")) {
            $play.setAttribute("class", "suspend");
            _this4.$player.play();
        } else {
            $play.setAttribute("class", "play");
            _this4.$player.pause();
        }
    });
};

